"use client"

import { useState } from "react"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"

interface ServiceDialogProps {
  open: boolean
  onOpenChange: (open: boolean) => void
}

export function ServiceDialog({ open, onOpenChange }: ServiceDialogProps) {
  const [loading, setLoading] = useState(false)
  const [formData, setFormData] = useState({
    customerId: "",
    technicianId: "",
    deviceType: "",
    brand: "",
    model: "",
    serialNumber: "",
    problem: "",
    priority: "medium",
    estimatedCompletionDate: "",
    cost: "",
    price: ""
  })

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    try {
      // Convert string numbers to actual numbers
      const numericFormData = {
        ...formData,
        cost: parseFloat(formData.cost),
        price: parseFloat(formData.price)
      }

      // TODO: Implement API call
      const response = await fetch("/api/services", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(numericFormData),
      })

      if (!response.ok) {
        throw new Error("Failed to create service")
      }

      onOpenChange(false)
      // TODO: Add success notification
    } catch (error) {
      console.error("Error creating service:", error)
      // TODO: Add error notification
    } finally {
      setLoading(false)
    }
  }

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-[500px] bg-black/90 border-purple-500/20">
        <DialogHeader>
          <DialogTitle className="text-white">Nova Manutenção</DialogTitle>
        </DialogHeader>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="customerId" className="text-white">Cliente</Label>
              <Select
                value={formData.customerId}
                onValueChange={(value) => setFormData({ ...formData, customerId: value })}
              >
                <SelectTrigger className="bg-white/10 border-purple-500/20 text-white">
                  <SelectValue placeholder="Selecione o cliente" />
                </SelectTrigger>
                <SelectContent>
                  {/* TODO: Add customer list from API */}
                  <SelectItem value="1">João Silva</SelectItem>
                  <SelectItem value="2">Maria Santos</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div className="space-y-2">
              <Label htmlFor="technicianId" className="text-white">Técnico</Label>
              <Select
                value={formData.technicianId}
                onValueChange={(value) => setFormData({ ...formData, technicianId: value })}
              >
                <SelectTrigger className="bg-white/10 border-purple-500/20 text-white">
                  <SelectValue placeholder="Selecione o técnico" />
                </SelectTrigger>
                <SelectContent>
                  {/* TODO: Add technician list from API */}
                  <SelectItem value="1">Carlos Técnico</SelectItem>
                  <SelectItem value="2">Ana Técnica</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div className="space-y-2">
              <Label htmlFor="deviceType" className="text-white">Tipo de Dispositivo</Label>
              <Input
                id="deviceType"
                value={formData.deviceType}
                onChange={(e) => setFormData({ ...formData, deviceType: e.target.value })}
                className="bg-white/10 border-purple-500/20 text-white"
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="brand" className="text-white">Marca</Label>
              <Input
                id="brand"
                value={formData.brand}
                onChange={(e) => setFormData({ ...formData, brand: e.target.value })}
                className="bg-white/10 border-purple-500/20 text-white"
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="model" className="text-white">Modelo</Label>
              <Input
                id="model"
                value={formData.model}
                onChange={(e) => setFormData({ ...formData, model: e.target.value })}
                className="bg-white/10 border-purple-500/20 text-white"
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="serialNumber" className="text-white">Número de Série</Label>
              <Input
                id="serialNumber"
                value={formData.serialNumber}
                onChange={(e) => setFormData({ ...formData, serialNumber: e.target.value })}
                className="bg-white/10 border-purple-500/20 text-white"
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="priority" className="text-white">Prioridade</Label>
              <Select
                value={formData.priority}
                onValueChange={(value) => setFormData({ ...formData, priority: value })}
              >
                <SelectTrigger className="bg-white/10 border-purple-500/20 text-white">
                  <SelectValue placeholder="Selecione a prioridade" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="low">Baixa</SelectItem>
                  <SelectItem value="medium">Média</SelectItem>
                  <SelectItem value="high">Alta</SelectItem>
                  <SelectItem value="urgent">Urgente</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div className="space-y-2">
              <Label htmlFor="estimatedCompletionDate" className="text-white">Previsão de Conclusão</Label>
              <Input
                id="estimatedCompletionDate"
                type="date"
                value={formData.estimatedCompletionDate}
                onChange={(e) => setFormData({ ...formData, estimatedCompletionDate: e.target.value })}
                className="bg-white/10 border-purple-500/20 text-white"
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="cost" className="text-white">Custo</Label>
              <Input
                id="cost"
                type="number"
                step="0.01"
                value={formData.cost}
                onChange={(e) => setFormData({ ...formData, cost: e.target.value })}
                className="bg-white/10 border-purple-500/20 text-white"
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="price" className="text-white">Preço</Label>
              <Input
                id="price"
                type="number"
                step="0.01"
                value={formData.price}
                onChange={(e) => setFormData({ ...formData, price: e.target.value })}
                className="bg-white/10 border-purple-500/20 text-white"
                required
              />
            </div>
          </div>
          <div className="space-y-2">
            <Label htmlFor="problem" className="text-white">Problema</Label>
            <Input
              id="problem"
              value={formData.problem}
              onChange={(e) => setFormData({ ...formData, problem: e.target.value })}
              className="bg-white/10 border-purple-500/20 text-white"
              required
            />
          </div>
          <div className="flex justify-end space-x-4">
            <Button
              type="button"
              variant="outline"
              onClick={() => onOpenChange(false)}
              className="border-purple-500/20 text-white hover:bg-purple-500/10"
            >
              Cancelar
            </Button>
            <Button
              type="submit"
              disabled={loading}
              className="bg-purple-600 hover:bg-purple-700"
            >
              {loading ? "Salvando..." : "Salvar"}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  )
}
