"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { ServiceDialog } from "@/components/dialogs/service-dialog"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { Badge } from "@/components/ui/badge"

type Service = {
  id: string
  customer: string
  deviceType: string
  brand: string
  model: string
  problem: string
  status: string
  priority: string
  technician: string
  createdAt: string
  price: number
}

const mockServices: Service[] = [
  {
    id: "1",
    customer: "João Silva",
    deviceType: "Smartphone",
    brand: "iPhone",
    model: "12 Pro",
    problem: "Tela quebrada",
    status: "in_progress",
    priority: "high",
    technician: "Carlos Técnico",
    createdAt: "2023-11-15",
    price: 899.90
  },
  {
    id: "2",
    customer: "Maria Santos",
    deviceType: "Smartphone",
    brand: "Samsung",
    model: "S21",
    problem: "Não carrega",
    status: "pending",
    priority: "medium",
    technician: "Carlos Técnico",
    createdAt: "2023-11-14",
    price: 199.90
  }
]

export default function ServicesPage() {
  const [search, setSearch] = useState("")
  const [services] = useState<Service[]>(mockServices)
  const [dialogOpen, setDialogOpen] = useState(false)

  const getStatusInfo = (status: string) => {
    const statusMap = {
      pending: { label: "Pendente", color: "bg-yellow-500" },
      in_progress: { label: "Em Andamento", color: "bg-blue-500" },
      completed: { label: "Concluído", color: "bg-green-500" },
      cancelled: { label: "Cancelado", color: "bg-red-500" }
    }
    return statusMap[status as keyof typeof statusMap]
  }

  const getPriorityInfo = (priority: string) => {
    const priorityMap = {
      low: { label: "Baixa", color: "bg-green-500" },
      medium: { label: "Média", color: "bg-yellow-500" },
      high: { label: "Alta", color: "bg-red-500" },
      urgent: { label: "Urgente", color: "bg-red-700" }
    }
    return priorityMap[priority as keyof typeof priorityMap]
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-semibold text-white">
          Manutenções
        </h1>
        <Button 
          className="bg-purple-600 hover:bg-purple-700"
          onClick={() => setDialogOpen(true)}
        >
          Nova Manutenção
        </Button>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Card className="p-6 bg-black/20 backdrop-blur-sm border-purple-500/20">
          <div className="flex items-center gap-4">
            <div className="p-3 bg-yellow-500/20 rounded-lg">
              <svg className="w-6 h-6 text-yellow-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
            <div>
              <p className="text-sm text-gray-400">Pendentes</p>
              <p className="text-2xl font-semibold text-white">5</p>
            </div>
          </div>
        </Card>

        <Card className="p-6 bg-black/20 backdrop-blur-sm border-purple-500/20">
          <div className="flex items-center gap-4">
            <div className="p-3 bg-blue-500/20 rounded-lg">
              <svg className="w-6 h-6 text-blue-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
              </svg>
            </div>
            <div>
              <p className="text-sm text-gray-400">Em Andamento</p>
              <p className="text-2xl font-semibold text-white">8</p>
            </div>
          </div>
        </Card>

        <Card className="p-6 bg-black/20 backdrop-blur-sm border-purple-500/20">
          <div className="flex items-center gap-4">
            <div className="p-3 bg-green-500/20 rounded-lg">
              <svg className="w-6 h-6 text-green-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
            <div>
              <p className="text-sm text-gray-400">Concluídos</p>
              <p className="text-2xl font-semibold text-white">12</p>
            </div>
          </div>
        </Card>

        <Card className="p-6 bg-black/20 backdrop-blur-sm border-purple-500/20">
          <div className="flex items-center gap-4">
            <div className="p-3 bg-purple-600/20 rounded-lg">
              <svg className="w-6 h-6 text-purple-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8c-1.657 0-3 .895-3 2s1.343 2 3 2 3 .895 3 2-1.343 2-3 2m0-8c1.11 0 2.08.402 2.599 1M12 8V7m0 1v8m0 0v1m0-1c-1.11 0-2.08-.402-2.599-1M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
            <div>
              <p className="text-sm text-gray-400">Faturamento</p>
              <p className="text-2xl font-semibold text-white">R$ 15.000</p>
            </div>
          </div>
        </Card>
      </div>

      <Card className="p-6 bg-black/20 backdrop-blur-sm border-purple-500/20">
        <div className="flex items-center gap-4 mb-6">
          <Input
            placeholder="Buscar manutenções..."
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            className="bg-white/10 border-purple-500/20 text-white"
          />
        </div>

        <div className="rounded-lg border border-purple-500/20 overflow-hidden">
          <Table>
            <TableHeader className="bg-purple-500/10">
              <TableRow>
                <TableHead className="text-gray-300">Cliente</TableHead>
                <TableHead className="text-gray-300">Dispositivo</TableHead>
                <TableHead className="text-gray-300">Problema</TableHead>
                <TableHead className="text-gray-300">Status</TableHead>
                <TableHead className="text-gray-300">Prioridade</TableHead>
                <TableHead className="text-gray-300">Técnico</TableHead>
                <TableHead className="text-gray-300">Data</TableHead>
                <TableHead className="text-gray-300">Valor</TableHead>
                <TableHead className="text-gray-300">Ações</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {services.map((service) => {
                const status = getStatusInfo(service.status)
                const priority = getPriorityInfo(service.priority)
                return (
                  <TableRow key={service.id} className="hover:bg-purple-500/5">
                    <TableCell className="text-white">{service.customer}</TableCell>
                    <TableCell className="text-gray-300">
                      {service.brand} {service.model}
                    </TableCell>
                    <TableCell className="text-gray-300">{service.problem}</TableCell>
                    <TableCell>
                      <Badge className={`${status.color} text-white border-none`}>
                        {status.label}
                      </Badge>
                    </TableCell>
                    <TableCell>
                      <Badge className={`${priority.color} text-white border-none`}>
                        {priority.label}
                      </Badge>
                    </TableCell>
                    <TableCell className="text-gray-300">{service.technician}</TableCell>
                    <TableCell className="text-gray-300">
                      {new Date(service.createdAt).toLocaleDateString()}
                    </TableCell>
                    <TableCell className="text-gray-300">
                      R$ {service.price.toFixed(2)}
                    </TableCell>
                    <TableCell>
                      <div className="flex items-center gap-2">
                        <Button variant="ghost" size="sm" className="text-purple-500 hover:text-purple-400 hover:bg-purple-500/10">
                          Editar
                        </Button>
                        <Button variant="ghost" size="sm" className="text-purple-500 hover:text-purple-400 hover:bg-purple-500/10">
                          Ver Detalhes
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                )
              })}
            </TableBody>
          </Table>
        </div>
      </Card>

      <ServiceDialog 
        open={dialogOpen}
        onOpenChange={setDialogOpen}
      />
    </div>
  )
}
